@import "src/assets/variables";

$track-height: 20px;
$track-width: 100px;
$thumb-width: 16px;

@mixin track {
  background: transparent;
  width: $track-width;
  height: 100%;
  transition: all .3s ease;
  cursor: default; }

@mixin thumb {
  border: none;
  border-radius: 50%;
  background: $orange;
  box-sizing: border-box;
  opacity: 0;
  margin-left: -($thumb-width / 2);
  width: $thumb-width;
  height: $thumb-width;
  cursor: default; }

.playerVolume {
  position: relative;
  z-index: 10;
  margin-left: 10px;

  &:hover .playerVolume__btn {
    opacity: 1;
    color: $orange; }

  &__btn {
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    border: none;
    background: transparent;
    opacity: .7;
    width: 40px;
    height: 40px;
    font-size: 20px;
    color: $white;
    cursor: pointer;
    transition: .3s;

    &:hover {
      opacity: 1;
      color: $orange; }

    &:active {
      top: 1px; } }

  &__inner {
    position: absolute;
    bottom: 80%;
    left: 50%;
    border-radius: 5px;
    background: transparentize($black, .3);
    opacity: 0;
    visibility: hidden;
    padding: 20px 0;
    height: #{$track-width + $track-height};
    width: #{$track-height * 2};
    transform: translateX(-50%);
    transition: .3s; }

  &:hover .playerVolume__inner {
    bottom: 110%;
    opacity: 1;
    visibility: visible; }

  &__line {
    position: relative;
    width: $track-width;
    height: $track-height;
    transform: rotate(-90deg) translate(#{$track-height - ($track-width / 2)}, #{$track-height - ($track-width / 2)});

    input {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 15;
      -webkit-appearance: none;
      border: none;
      background: transparent;
      padding: 0;
      width: 100%;
      height: 100%;

      &:focus {
        background: transparent; }

      &::-moz-focus-outer {
        border: 0;
        background: transparent; }

      &::-webkit-slider-runnable-track {
        @include track;
        background: transparent;
        border: 0;
        border-radius: 0; }

      &::-webkit-slider-thumb {
        @include thumb;
        -webkit-appearance: none; }

      &::-moz-range-track {
        @include track;
        background: transparent;
        border: 0;
        border-radius: 0; }

      &::-moz-range-thumb {
        @include thumb; }

      &::-ms-track {
        @include track;
        background: transparent;
        border-color: transparent;
        border-width: 0;
        color: transparent; }

      &::-ms-fill-lower {
        background: transparent;
        border: none;
        border-radius: 0; }

      &::-ms-fill-upper {
        background: transparent;
        border: none;
        border-radius: 0; }

      &::-ms-thumb {
        @include thumb; }

      &:focus {
        background: transparent; } }

    &Progress, &Before {
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -5px;
      min-height: 10px;
      width: 0;
      transition: min-height .3s, margin-top .3s; }

    &Progress {
      z-index: 10;
      background: $orange; }

    &Before {
      z-index: 3;
      background: $white;
      opacity: .5;
      width: 100%; }

    &:hover {
      .playerVolume__line {
        &Progress, &Before {
          margin-top: -5px;
          min-height: 10px; } } }

    &.muted .playerVolume__lineProgress {
      opacity: 0; } } }

.playerResize {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: transparent;
  opacity: .7;
  width: 40px;
  height: 40px;
  font-size: 20px;
  color: $white;
  cursor: pointer;
  transition: .3s;

  &:hover {
    opacity: 1;
    color: orange; }

  &:active {
    top: 1px; } }


.tablet, .mobile {
  .playerVolume__inner {
    display: none; } }
