@import "src/assets/variables";

.pageLesson {
  .loaderVideo {
    margin: 50px 0;
    padding-top: 50%;
    width: 100%;
    height: 0; }

  .loaderTest {
    display: flex;
    align-items: center;
    border: 3px solid $orange;
    margin: 50px 0 0;
    padding: 30px 50px;
    width: 100%;
    font-size: 0;

    &__icon {
      border-radius: 50%;
      margin: 0;
      width: 65px;
      height: 65px; }

    &__text {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 0 25px;
      width: 100%;
      max-width: calc(100% - 65px - 210px);
      height: 60px;

      .loaderBlock {
        margin: 0;
        height: 25px;

        &:first-child {
          width: 90%; }

        &:last-child {
          width: 75%; } } }

    &__btn {
      margin: 0;
      width: 210px;
      height: 65px; } } }

.lessonVideo {
  .player {
    background: $black;
    padding-top: 0;
    height: 100%; } }

@media (max-width: 1919px) {
  .pageLesson {
    .loaderTest {
      padding: 20px 30px;

      &__text {
        max-width: calc(100% - 65px - 180px); }

      &__btn {
        width: 180px; } } } }

@media (max-width: 1199px) {
  .pageLesson {
    .loaderTest {
      flex-wrap: wrap;
      padding: 15px 20px;

      &__text {
        padding: 0 15px;
        width: calc(100% - 65px);
        max-width: none; }

      &__btn {
        margin-top: 20px;
        margin-left: 80px;
        width: 180px; } } } }

@media (max-width: 767px) {
  .pageLesson {
    .loaderTest {
      &__icon {
        width: 50px;
        height: 50px; }

      &__text {
        width: calc(100% - 50px); }

      &__btn {
        margin-left: 65px;
        height: 50px; } } } }

@media (max-width: 479px) {
  .pageLesson {
    .loaderTest {
      &__icon {
        margin-bottom: 20px; }

      &__text {
        padding: 0;
        width: 100%; }

      &__btn {
        margin-left: 0; } } } }
