.loaderBlock {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  background: rgba(0, 0, 0, .05);
  margin: 0 5px;
  max-width: 100%;

  &:first-child {
    margin-left: 0; }

  &:last-child {
    margin-right: 0; }

  div {
    position: absolute;
    top: -25%;
    left: 20%;
    width: 40%;
    height: 150%;
    background: rgba(255, 255, 255, .7);
    box-shadow: 0 0 50px 50px rgba(255, 255, 255, .7);
    transform: rotate(25deg);
    animation: flickering 1s infinite linear; } }

@keyframes flickering {
  from {
    left: calc(-100% - 100px); }
  to {
    left: calc(100% + 100px); } }
