@import "src/assets/variables";

.lessonVideo {
  &__video.noAccess {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__noAccess {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
  }

  &__list {
    .element {
      &__number {
        display: inline-flex;
        align-items: center;
        width: 100%;
        transition: 0.3s;

        .icon {
          align-self: flex-start;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          border-radius: 50%;
          width: 20px;
          height: 20px;
          color: $white;

          &-check {
            background: $green;
            font-size: 6px;
          }

          &-play {
            background: $orange;
            font-size: 8px;
          }

          &-lock {
            background: $gray;
            font-size: 10px;
          }
        }

        .subtitle {
          padding-left: 10px;
          width: calc(100% - 20px);
        }
      }

      &.active,
      &.locked {
        cursor: default;

        &:hover {
          .text {
          }
          .element__title {
            color: $black;
          }
        }
      }

      &.active {
        .text {
          background: $green;
          padding: 20px;
        }

        .element__number {
          .icon-check {
            background: $green-dark;
          }

          .subtitle {
            color: $white;
          }
        }
      }

      &.locked {
        &:hover {
          .text {
            background: transparent;
            padding: 20px 40px 20px 0;
          }
        }
      }
    }
  }
}
