@import "src/assets/variables";

.player {
  position: relative;
  overflow: hidden;
  padding-top: 56.25%;

  &Overlay {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10; }

  &PlayButtonBig {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10; }

  .reactPlayer {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5; }

  &__text {
    display: none;
    position: absolute;
    left: 0;
    bottom: 100%;
    z-index: 5;
    background: transparentize($black, .3);
    padding: 10px 20px;
    width: 100%; }

  &__title {
    margin: 0;
    font-size: 20px;
    color: $white; }

  &__description {
    margin: 10px 0 0;
    font-size: 14px;
    color: $white; }

  &__controls {
    display: flex;
    align-items: center;
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 15;
    background: transparentize($black, .3);
    padding: 10px;
    width: 100%;
    height: 40px;
    transition: .3s;

    &:hover {
      transform: translateY(-100%); }

    .playSmall {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border: none;
      background: transparent;
      opacity: .7;
      margin-right: 10px;
      width: 40px;
      height: 40px;
      font-size: 20px;
      color: $white;
      cursor: pointer;
      transition: .3s;

      &:hover {
        opacity: 1;
        color: $orange; }

      &:active {
        top: 1px; } }


    .PlayerProgressLine {
      width: calc(100% - 80px); } }

  &.paused {
    .player {
      &PlayButtonBig {
        visibility: visible;
        opacity: 1; } } }

  &.touched {
    &.paused, &.hovered {
      .player {
        &__controls {
          transform: translateY(-100%); } } }

    &.paused {
      .player {
        &__text {
          display: block; } } } } }
