@import "src/assets/variables";

.videosTableLine {
  .col {
    border: 0;
    border-top: 1px solid $gray;
    border-collapse: collapse;
    transition: .3s;
    cursor: pointer;

    &-img {
      vertical-align: top;
      padding: 10px 10px 10px 0;
      width: 120px;
      font-size: 0;

      img {
        width: 100%;
        min-width: 70px;
        height: auto; }

      .playButton {
        position: absolute;
        top: 50%;
        left: 50%;
        opacity: .7;
        padding-left: 44px;
        transform: translate(-50%, -50%);
        transition: .3s; } }

    &-name {
      margin: 0;
      padding: 10px 10px 10px 0;
      width: calc(100% - 120px);

      .title {
        margin: 0;
        padding: 0 20px 0 0;
        font-weight: 500;
        transition: .3s; }

      .time {
        align-self: baseline;
        margin: 0;
        padding-left: 10px;
        color: $darkgray; } } }

  &__img {
    position: relative; }

  &__name {
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-height: 100%; }

  &:first-child .col {
    border-top: none; }

  &:hover,
  &:hover + .videosTableLine {
    .col {
      border-top-color: transparent; } }

  &:hover {
    .col {
      background: $white;

      &-img {
        padding-left: 10px;
        padding-right: 0;

        .playButton {
          opacity: 1; } }

      &-name {
        .title {
          padding-left: 10px;
          padding-right: 10px;
          color: $orange; } } } } }

@media (max-width: 567px) {
  .videosTableLine {
    .col {
      &-img {
        .playButton {
          padding-left: 34px;

          .icon {
            width: 40px;
            height: 40px; } } }

      &-name {
        .title,
        .time {
          font-size: 14px; } } } } }

@media (max-width: 479px) {
  .videosTableLine {
    .col {
      &-name {
        .title,
        .time {
          font-size: 13px; }

        .time {
          margin-top: 10px;
          padding-left: 0;
          padding-right: 10px;
          transition: .3s; } } }

    &__name {
      flex-direction: column; }


    &:hover {
      .col-name {
        .time {
          padding-left: 10px;
          padding-right: 0; } } } } }
