@import "src/assets/variables";

.playerOverlay {
  position: relative;
  width: 100%;
  height: 100%;

  .before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    background: no-repeat center;
    background-size: cover;
    width: 100%;
    height: 100%;
    transition: .3s; }

  .playerPlayButtonBig {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10; }

  &:hover {
    .playerPlayButtonBig {
      background: $orange; } } }
