@import "src/assets/variables";

.test {
  border: 3px solid $orange;
  margin: 50px 0 0;
  padding: 30px 50px;
  width: 100%;

  &__preview {
    display: flex;
    align-items: center;

    .icon {
      align-self: flex-start;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: $gray;
      width: 65px;
      height: 65px;
      color: $white;

      &-check {
        background: $green;
      }

      &-close {
        background: $red;
      }
    }

    .text {
      margin: 0;
      padding: 0 25px;
      max-width: calc(100% - 65px - 210px);
      font-size: 28px;
      font-weight: 500;
      color: $black;
    }

    .button {
      align-self: flex-start;
      margin-left: auto;
      width: 210px;
    }
  }

  &__inner {
    margin: 30px 0;
    padding: inherit;
    background: $gray;
  }

  &__buttons {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &Count {
      margin-left: auto;
      white-space: nowrap;
      font-size: 22px;
      color: $darkgray;
    }

    .button {
      white-space: nowrap;
      width: 220px;
      height: 50px;
    }

    &.finished {
      flex-wrap: wrap;
      justify-content: flex-start;
      margin-top: 15px;

      .button {
        margin: 15px 15px 0 0;
      }
    }
  }

  &__buttonPrev {
    margin-right: 25px;
  }

  &__buttonNext {
    margin-left: 25px;
  }

  &Question {
    &__top {
      display: flex;
      align-items: baseline;
    }

    &__question {
      margin-bottom: 40px;
      padding-right: 40px;
      font-size: 16px;
    }

    &__points {
      margin-left: auto;
      white-space: nowrap;
      color: $darkgray;
    }

    &__img {
      margin-bottom: 40px;
      max-width: 600px;
    }

    &__answers {
      .checkbox,
      .radio {
        display: block;
        margin-bottom: 20px;
      }

      .checkboxesList,
      .radioButtonsList {
        column-count: 2;
        column-gap: 40px;
        width: 100%;
      }

      &.scale .radioButtonsList {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin: 0 -10px;
        width: calc(100% + 20px);
        column-count: 0;
        column-gap: 0;

        .radio {
          margin: 0 10px 20px;
          width: auto;

          &__inner .text {
            justify-content: center;
            padding-top: 40px;
            padding-left: 0;
            min-width: 32px;
          }
        }
      }
    }

    &__finishAnswer {
      display: flex;
      align-items: baseline;

      .icon {
        margin-right: 10px;
        font-size: 14px;
        color: $green;
      }
    }
  }

  &__finishedScoresSubtitle {
    margin: 30px 0 0;
    color: $darkgray;
  }

  &__finishedScores {
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 32px;
  }

  &__finishedText {
    font-weight: 600;
  }
}

@media (max-width: 1919px) {
  .test {
    padding: 20px 30px;

    &__preview {
      .text {
        max-width: calc(100% - 65px - 180px);
        font-size: 22px;
      }

      .button {
        width: 180px;
      }
    }

    &__inner {
      margin: 20px 0;
    }
  }
}

@media (max-width: 1199px) {
  .test {
    padding: 15px 20px;

    &__preview {
      flex-wrap: wrap;

      .text {
        padding: 0 15px;
        width: calc(100% - 65px);
        max-width: none;
        font-size: 22px;
      }

      .button {
        margin-top: 20px;
        margin-left: 80px;
        width: 180px;
      }
    }

    &__inner {
      margin: 15px 0;
    }
  }
}

@media (max-width: 991px) {
  .test {
    &__preview .text {
      font-size: 16px;
    }

    &Question {
      &__answers {
        column-count: 1;
      }
    }
  }
}

@media (max-width: 767px) {
  .test {
    &__buttons {
      flex-direction: column;
      align-items: flex-end;

      .button {
        width: 100%;
      }
    }

    &__buttonPrev {
      margin-right: 0;
      margin-bottom: 15px;
      order: 2;
    }

    &__buttonNext {
      order: 3;
    }

    &__buttonsCount {
      order: 1;
      margin-right: 0;
      margin-bottom: 15px;
    }
  }
}

@media (max-width: 567px) {
  .test {
    &__preview {
      .icon {
        width: 50px;
        height: 50px;
      }

      .text {
        width: calc(100% - 50px);
        font-size: 18px;
      }

      .button {
        margin-left: 65px;
        height: 50px;
      }
    }

    &Question {
      &__top {
        display: block;
      }

      &__question {
        padding-right: 0;
        margin-bottom: 10px;
      }

      &__points {
        display: inline-block;
        margin-bottom: 20px;
        width: 100%;
      }
    }
  }
}

@media (max-width: 479px) {
  .test {
    &__preview {
      .icon {
        margin-bottom: 20px;
      }

      .text {
        padding: 0;
        width: 100%;
      }

      .button {
        margin-left: 0;
      }
    }
  }
}

@media (max-width: 399px) {
  .test {
    &Question {
     &__answers {
       .checkbox__inner .text,
       .radio__inner .text {
         font-size: 14px;
       }
     }
    }
  }
}
