@import "src/assets/variables";

.playerPlayButtonBig {
  position: relative;
  border: none;
  border-radius: 50%;
  background: $green;
  visibility: hidden;
  opacity: 0;
  width: 90px;
  height: 90px;
  font-size: 15px;
  color: $white;
  transform: translate(-50%, -50%);
  transition: .3s;
  cursor: pointer;

  &:hover {
    background: $orange; } }
