@import "src/assets/variables";

.loadingList {
  margin-top: 50px;

  &__element {
    display: flex;
    position: relative;
    border-bottom: 1px solid $gray;
    margin-bottom: 30px;
    padding-bottom: 30px;

    .loaderBlock {
      height: 70px;

      &:first-child {
        border-radius: 35px;
        width: 70px; }

      &:last-child {
        margin-left: 20px;
        width: 300px;
        max-width: calc(100% - 90px); } } } }
