@import "src/assets/variables";

$track-height: 20px;
$track-width: 100% !default;
$thumb-width: 16px;

@mixin track {
  background: transparent;
  width: $track-width;
  height: 100%;
  transition: all .3s ease;
  cursor: default; }

@mixin thumb {
  border: none;
  border-radius: 50%;
  background: $orange;
  box-sizing: border-box;
  opacity: 0;
  margin-left: -($thumb-width / 2);
  width: $thumb-width;
  height: $thumb-width;
  cursor: default; }

.PlayerProgressLine {
  position: relative;
  height: $track-height;

  input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 15;
    -webkit-appearance: none;
    border: none;
    background: transparent;
    padding: 0;
    width: 100%;
    height: 100%;

    &:focus {
      background: transparent; }

    &::-moz-focus-outer {
      border: 0;
      background: transparent; }

    &::-webkit-slider-runnable-track {
      @include track;
      background: transparent;
      border: 0;
      border-radius: 0; }

    &::-webkit-slider-thumb {
      @include thumb;
      -webkit-appearance: none; }

    &::-moz-range-track {
      @include track;
      background: transparent;
      border: 0;
      border-radius: 0; }

    &::-moz-range-thumb {
      @include thumb; }

    &::-ms-track {
      @include track;
      background: transparent;
      border-color: transparent;
      border-width: 0;
      color: transparent; }

    &::-ms-fill-lower {
      background: transparent;
      border: none;
      border-radius: 0; }

    &::-ms-fill-upper {
      background: transparent;
      border: none;
      border-radius: 0; }

    &::-ms-thumb {
      @include thumb; }

    &:focus {
      background: transparent; } }

  &__progress, &__buffer, &__before {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -2px;
    min-height: 4px;
    width: 0;
    transition: min-height .3s, margin-top .3s; }

  &__progress {
    background: $orange;
    z-index: 10; }

  &__buffer {
    z-index: 5;
    background: $green;
    opacity: .3; }

  &__before {
    z-index: 3;
    background: $white;
    opacity: .5;
    width: 100%; }

  &:hover {
    .PlayerProgressLine {
      &__progress, &__buffer, &__before {
        margin-top: -5px;
        min-height: 10px; } } } }
